import React, { useState } from "react";
import "./Modal.css";
import close from "../../img/close.svg";
import s from './ModalTypeOne.module.sass'
export  function ModalTypeTwo({modal,setModal,packageStatus}) {
    const toggleModal = () => {
        setModal(!modal);
    };

    if (modal) {
        document.body.classList.add('active-modalMy')
    } else {
        document.body.classList.remove('active-modalMy')
    }

    function statusDelivery(){
        if (packageStatus === ' '){
            return <>   ,  <br/>   .</>
        }
        else if(packageStatus?.split(' ')[0] === ""){
            return <> {packageStatus},  <br/>   .</>
        }
        else if(packageStatus?.split(' ')[0] === ""){
            return <>{packageStatus}</>
        }
        else if(packageStatus?.split(' ')[0] === ""){
            return <>{packageStatus}</>
        }
        else if(packageStatus?.split(' ')[0] === ""){
            return <>{packageStatus}</>
        }
        else {
            return <>      :( <br/>  </>
        }
    }

    return (
        <>

            {modal && (
                <div className="myModal">
                    <div className="overlay">
                        <div className="modal-contentMy">
                            <div className={s.title}> </div>
                            <p  className={s.subTitle}>
                                {statusDelivery()}
                                {/*{packageStatus === " " ? <>   ,  <br/>   .</>: null}*/}
                                {/*{packageStatus.split(' ')[0] === "" ? <> {packageStatus},  <br/>   .</>: null}*/}
                                {/*{packageStatus !== " " ||(packageStatus.split(' ')[0] !== "" )? <>      :( <br/>  </>: null}*/}
                            </p>
                            <div className="close-modal" onClick={toggleModal}>
                                <img src={close} alt="close"/>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </>
    );
}