import React from 'react';
import s from './Information.module.sass'
import {useNavigate} from "react-router";

export const Information = ({setBiggesModal}) => {
    const navigator = useNavigate()
    return (
        <div className={s.main}>
            <h2 className={s.title}></h2>
            <div className={s.buttons}>
                <div>
                    <button onClick={() => setBiggesModal(true)}> </button>
                    <button onClick={() => navigator('/tarifComission')}>  </button>
                    <button onClick={() => navigator('/returns')}></button>
                </div>
                <div>
                    <button onClick={() => navigator('/faq')}>FAQ</button>
                    <button onClick={() => navigator('/bannedProducts')}>  </button>
                    <button  onClick={() => navigator('/politics')}></button>
                </div>
            </div>
        </div>
    );
};
