import React from 'react';
import s from './CashBack.module.sass'
import visa from '../../../src/img/logos/visa.png'
import telegram from "../../img/telegram.svg";
import whatsApp from "../../img/whatsApp.svg";
import {Gallery} from "../Gallery/Gallery";

export const CashBack = () => {
    return (
        <div className={s.main}>
            <img className={s.image} src={visa} alt="visa image"/>
            <div className={s.title}> 10%        .</div>
            <div className={s.part}>
                <div className={s.partTitle}>:        <a href="https://t.me/shop_abroad_support"></a>     10%     </div>
                <div className={s.partSubtitle}>            10%     </div>
                <Gallery/>
            </div>

        </div>
    );
};
