import React from 'react';
import s from './Footer.module.sass'
import avito from '../../../src/img/avito.svg'
export const Footer = ({changeUrl}) => {
    return (
        <footer>
            <div className={s.link}>
                <div onClick={() => changeUrl('/politics')} > </div>
                <div onClick={() => changeUrl('/agreement')} >    </div>
                <div onClick={() => changeUrl('/vat')} >  | VAT</div>
            </div>

            <div className={s.buttons}>
                {/*<button > TLG </button>*/}
                <button onClick={() => changeUrl('/list')}> </button>
                <button onClick={() => changeUrl('/contact')}></button>
                <button onClick={() => changeUrl('/information')}></button>
                <button onClick={() => changeUrl('/cashback')}>CashBack</button>
            </div>
            {/*<a href="#"><img src={avito} alt="avito"/></a>*/}
        </footer>
    );
};
