import React, {useState} from 'react';
import s from './Commission.module.sass'
import Accordion from "react-bootstrap/Accordion";


export const Commission = () => {
    return (
        <div className={s.main}>
            <div  className={s.title}>
                 
            </div>
            <div className={s['wrapper']}>
                <div className={s['container']}>
                    <Accordion   defaultActiveKey="0">
                        <Accordion.Item eventKey="0">
                            <Accordion.Header>,   :  5  15%</Accordion.Header>
                            <Accordion.Body>
                                <div className={s.clasicSub}> </div>
                                <ul className={s.uls}>
                                    <li> 100  15%</li>
                                    <li> 100  300  10%</li>
                                    <li> 300  500  7%</li>
                                    <li> 500  5%</li>
                                </ul>
                            </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="1">
                            <Accordion.Header>:  3  15%</Accordion.Header>
                            <Accordion.Body>
                                <ul className={s.uls}>
                                    <li> 100  15%</li>
                                    <li> 100  300  12%</li>
                                    <li> 300  500  10%</li>
                                    <li> 500  1000  7%</li>
                                    <li> 1000  1500  5%</li>
                                    <li> 1500  3%</li>
                                </ul>
                            </Accordion.Body>
                        </Accordion.Item>

                        <Accordion.Item eventKey="2">
                            <Accordion.Header>:  10  15%</Accordion.Header>
                            <Accordion.Body>
                                <div className={s.clasicSub}> </div>
                                <ul className={s.uls}>
                                    <li> 1000  15%</li>
                                    <li> 1000  10%</li>
                                </ul>
                            </Accordion.Body>
                        </Accordion.Item>

                        <Accordion.Item eventKey="3">
                            <Accordion.Header>: 15%</Accordion.Header>
                            <Accordion.Body>
                                <div className={s.clasicSub}>      15%</div>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>

                    {/*<div className={s.clasicSub}>     3  15%   ,   </div>*/}
                    {/*<div className={s.mainText}>   15</div>*/}
                    {/*<div className={s.mainText}>-,   :  5  15%</div>*/}
                    {/*<div className={s.clasicSub}> </div>*/}
                    {/*<ul className={s.uls}>*/}
                    {/*    <li> 100  15%</li>*/}
                    {/*    <li> 100  300  10%</li>*/}
                    {/*    <li> 300  500  7%</li>*/}
                    {/*    <li> 500  5%</li>*/}
                    {/*</ul>*/}
                    {/*<div className={s.mainText}>:  3  15% </div>*/}
                    {/*<div className={s.clasicSub}> </div>*/}
                    {/*<ul className={s.uls}>*/}
                    {/*    <li> 100  15%</li>*/}
                    {/*    <li> 100  300  12%</li>*/}
                    {/*    <li> 300  500  10%</li>*/}
                    {/*    <li> 500  1000  5%</li>*/}
                    {/*    <li> 500  1000  5%</li>*/}
                    {/*    <li> 1500  3%</li>*/}
                    {/*</ul>*/}
                    {/*<div className={s.mainText}>:  10  15%</div>*/}
                    {/*<div className={s.clasicSub}> </div>*/}
                    {/*<ul className={s.uls}>*/}
                    {/*    <li> 1000  15%</li>*/}
                    {/*    <li> 1000  10%</li>*/}
                    {/*</ul>*/}
                    {/*<div className={s.mainText}>-: 15%</div>*/}
                </div>
            </div>
        </div>
    );
};
